.header {
    min-height: 230px;
    padding: 40px 0 0 0;
    background-image: url("../images/home/bg-image-mob.png");
    background-size: cover;
    background-repeat: no-repeat;

    @media screen and(min-width:768px) {
        background-image: url("../images/home/bg-image-tablet.png");
    }
    @media screen and(min-width:1280px) {
        background-image: url("../images/home/bg-image-desctop.png");
    }
}
.header__wrapp {
    display: flex;
    margin: 0 0 54px;
    justify-content: space-between;
}

.header__link {
    display: flex;
    gap: 8px;
    align-items: center;
    text-decoration: none;
}
.header__logo-text {
    display: none;

    font-weight: 500;
    font-size: 30px;
    line-height: calc(35 / 30);
    color: $main-color;

    @media screen and(min-width:768px) {
        display: inherit;
    }
}

.nav {
    padding: 4px 0 0 0;
}

.nav__link {
    font-weight: 500;
    font-size: 12px;
    line-height: calc(14 / 12);

    text-decoration: none;
    text-transform: uppercase;
    color: $main-color;
    transition: color 250ms linear;
}
.nav__link:hover,
.nav__link:focus {
    color: $red-line-color;
}

.nav__link--active {
    border-bottom: 3px solid $red-line-color;
}

.nav__list {
    display: flex;
    gap: 40px;
}

.search-form {
    margin: 0 auto;
}

.search-form {
    position: relative;
    margin: 0 auto 16px;
    max-width: 336px;

    @media screen and(min-width:1280px) {
        max-width: 394px;
    }
}

.search-form__input {
    width: 100%;
    padding: 0 40px 4px 0;
    font-size: 14px;
    line-height: calc(16 / 14);
    color: $main-color;
    border: none;
    outline: none;
    border-bottom: 1px solid $main-color;
    background-color: transparent;
}

.search-form__input::placeholder {
    font-size: 14px;
    line-height: calc(16 / 14);
    color: $main-color;
}

.search-form__button {
    position: absolute;
    right: -14px;
    bottom: -10px;

    display: inline-block;
    width: 40px;
    height: 40px;
    padding: 0;

    text-align: center;
    vertical-align: middle;

    background-color: transparent;
    border: none;

    cursor: pointer;
}
.search-form__icon {
    display: block;
    margin: 0 auto;
}

.message {
    display: block;
    max-width: 336px;
    margin: 0 auto;
    font-size: 14px;
    line-height: calc(16 / 14);
    color: $red-line-color;
    text-align: left;

    @media screen and(min-width:768px) {
        max-width: none;
        text-align: center;
    }
}
